// 
import { BasicTable } from './Table/index.js'
import { BasicForm } from './Form/index.js'
import { BasicDialog } from './Dialog/index'
import {TreeSelect} from './Tree/index'
// 
const components = [
    BasicTable,
    BasicForm,
    BasicDialog,
    TreeSelect
]

//  install  Vue  use 
const install = function (Vue) {
  // 
  if (install.installed) return
  // 
  components.map(component => {
     Vue.component(component.name, component)})
}

// 
if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue)
}

export {
  //  install Vue.use() 
  install,
  BasicForm,
  // 
  BasicTable,
  BasicDialog
}

export default{
  install
}