<template>
  <div class="tree-select">
    <el-select
      v-model="treeValue"
      :multiple="multiple"
      :collapse-tags="collapseTags"
      :disabled="disabled"
      @clear="handleSelectClear"
      :clearable="true"
      ref='treeSelect'
      default-expand-all
      :style="{ width: width }"
      @remove-tag="selectRemoveTag"
      :placeholder="placeholder"
      :popper-append-to-body="true"
    >
      <div class="tree-select__search" v-if="filterable">
        <el-input
          v-model="filterText"
          placeholder=""
          clearable
          size="small"
        ></el-input>
      </div>

      <el-option v-model="treeDataValue" class="tree-select__options">
        <div v-show="data[0]">
          <el-tree
            v-on="$listeners"
            v-bind="$attrs"
            :lazy="lazy"
            :node-key="nodeKey"
            :filter-node-method="filterNode"
            :data="treeData"
            :default-checked-keys="defaultCheckedKeys"
            :props="props"
            @node-click="handleNodeClick"
            :show-checkbox="multiple"
            ref="tree"
            @check="getHalfCheckedNodes"
          >
          </el-tree>
        </div>
      </el-option>

      <el-option
        v-for="(item, index) of treeOptionsArray"
        :key="index"
        v-show="false"
        :value="item[props.value]"
        :label="item[props.label]"
      >
      </el-option>
    </el-select>
  </div>
</template>
<script>
import { handleTree } from "../utils.js";
export default {
  name: "basicTree",
  data() {
    return {
      method: null,
      filterText: "",
      treeValue: [], // treevalue 
      treeData: [], // tree
      treeDataValue: "", // 
      treeOptionsArray: [], // options select ]
      ids: [], //id
    };
  },
  props: {
    // 
    clearable: {
      type: Boolean,
      default: () => {
        return true;
      },
    },
    // 
    disabled: {
      type: Boolean,
      default: () => {
        return false;
      },
    },
    //  true +x  false 
    collapseTags: {
      type: Boolean,
      default: () => {
        return false;
      },
    },
    //
    filterable: {
      type: Boolean,
      default: () => {
        return false;
      },
    },
    nodeKey: {
      type: String,
    },

    filterSelectKey: {
      //
      type: String,
    },
    placeholder: {
      type: String,
      default: () => {
        return "";
      },
    },
    width: {
      type: String,
      default: () => {
        return "100%";
      },
    },
    props: {
      type: Object,
    },
    refs: {
      required: false,
      type: String,
    },
    data: {
      //  Tree 
      type: Array,
      default: () => {
        return [];
      },
    },
    multiple: {
      //
      type: Boolean,
      default: () => {
        return false;
      },
    },
    defaultCheckedKeys: {
      //
      type: Array,
    },
    format: {
      type: Array,
    },
    lazy: {
      //
      type: Boolean,
      default: () => {
        return false;
      },
    },
  },
  watch: {
    treeValue: {
      handler(val) {
        this.getPropsTreeKeys();
      },
    },
    filterText(val) {
      console.log(val);
      console.log(this.$refs.tree);
      this.$refs.tree.filter(val);
    },
  },
  mounted() {
    this.method = this.$refs.tree;
    this.handleFormat();
    // setTimeout(() => {

    // }, 500);
  },
  methods: {
    // 
    filterNode(value, data) {
      console.log(data);
      if (!value) return true;
      return data[this.props.label].indexOf(value) !== -1;
    },
    // TreeValue
    clearTreeValue() {
      this.treeValue = [];
      this.handleSelectClear();
    },
    /**
     * select  Treevalue
     */
    handleSelectClear() {
      this.$emit("clearTreeValue");
      if (!this.treeValue) {
        this.$refs.tree.setCheckedKeys([]);
        // if(this.multiple){

        // }else{
        //    this.$refs.tree.setCheckedKeys("")
        // }
        return;
      }
      console.log(this.treeValue);
      this.$refs.tree.setCheckedKeys(this.treeValue);
    },
    /**
     * Tree
     */
    handleFormat() {
      //
      if (this.lazy && this.defaultCheckedKeys && this.defaultCheckedKeys[0]) {
        let deepArray = JSON.parse(JSON.stringify(this.defaultCheckedKeys));
        for (let item of this.data) {
          if (this.defaultCheckedKeys.indexOf(item[this.props.value]) > -1) {
            this.treeOptionsArray.push(item);
          }
        }
        if (!this.multiple) {
          this.treeValue = Number(this.defaultCheckedKeys.join(","));
        } else {
          this.treeValue = this.defaultCheckedKeys;
        }
      }

      if (this.format) {
        if (this.lazy) {
          this.treeData = this.handleTree(this.data, ...this.format);
        } else {
          this.treeData = handleTree(this.data, ...this.format);
        }
      } else {
        this.treeData = this.data;
      }

      //    
      this.$nextTick(() => {
        this.$emit("renderOver"); //
        setTimeout(() => {
          // console.log(this.treeData);
        }, 3000);
        let TreeCheckoutObj = {
          checkedKeys: this.$refs.tree.getCheckedKeys(),
          checkedNodes: this.$refs.tree.getCheckedNodes(),
        };
        // 
        if (this.multiple) {
          this.getHalfCheckedNodes("", TreeCheckoutObj);
        } else {
          for (let i = 0; i < TreeCheckoutObj.checkedNodes.length; i++) {
            // if (
            //   TreeCheckoutObj.checkedNodes[i][this.props.value] ===
            //   TreeCheckoutObj.checkedKeys[0]
            // ) {
            //   this.handleNodeClick(TreeCheckoutObj.checkedNodes[i]);
            // }
            if (
              TreeCheckoutObj.checkedNodes[i][this.props.value] ===
              this.defaultCheckedKeys[0]
            ) {
              this.handleNodeClick(TreeCheckoutObj.checkedNodes[i]);
            }
          }
        }
      });
    },
    /**
     * treeselect
     * @param val id
     */
    selectRemoveTag(val) {
      // console.log(this.treeValue);
      this.$refs.tree.setCheckedKeys(this.treeValue);
      this.$emit("remove-tag", val);
    },
    /**
     * checkbox 
     * @param val 
     * @param  data Key  
     */
    getHalfCheckedNodes(val, data) {
      if (!this.lazy) {
        this.treeOptionsArray = [];
      }
      // v-model
      if (this.multiple) {
        if (!this.lazy) {
          this.treeValue = [];
        }
      } else {
        this.treeValue = "";
      }

      this.ids = [];
      let checkedNodes = data.checkedNodes.filter((item) => {
        if (item[this.props.value]) {
          this.ids.push(item[this.nodeKey]);

          this.ids = [...new Set(this.ids)];
          return item;
        }
      });
      if (!this.lazy) {
        this.treeOptionsArray = data.checkedNodes;
      }

      this.$nextTick(() => {
        if (this.multiple) {
          // 
          if (this.lazy) {
            // console.log(this.ids);
            // if (!this.multiple) {
            // console.log(this.ids);
            // this.treeValue = this.ids;
            // }
          } else {
            this.treeValue = data.checkedKeys;
          }
        } else {
          this.treeValue = data.checkedKeys.join();
        }
        // 
        if (val) {
          this.$emit("handleCheckDom");
        } else {
          this.$emit("handleCheckDom", "default");
        }
      });
    },
    /**
     *  
     * @param 
     */
    handleNodeClick(data) {
      //  
      if (this.filterSelectKey) {
        if (this.filterSelectKey[0] === "!") {
          if (
            data[this.filterSelectKey.substring(1, this.filterSelectKey.length)]
          ) {
            return;
          }
        } else {
          if (!data[this.filterSelectKey]) {
            return;
          }
        }
      }
      if (!this.multiple) {
        this.treeOptionsArray = [];
        this.treeOptionsArray.push(data);
        this.treeValue = data[this.props.value];
        this.$refs.treeSelect.blur();
      }
    },
    /**
     * Tree
     *
     */
    getPropsTreeKeys() {
      this.$emit("getTreeValue", this.treeValue);
    },
  },
};
</script>

<style lang="scss" scoped>
.tree-select {
  &__search {
    width: 98%;
    margin: 0 auto;
  }
  &__options {
    height: auto;
    background-color: #fff;
  }
  &__options:hover {
    background-color: #fff;
  }
}
</style>
